import { FaTimes } from 'react-icons/fa'
import MyTasks from './Tasks'
export default function Task({task, onDeletee, onToggle}){
    return(
        <div className={`task ${task.reminder ? 'reminder':''}`} key={MyTasks.id} onDoubleClick={() => onToggle(task.id)}>
            <h3>{task.text}<FaTimes style={{color:"red ", cursor:'pointer'}} onClick={() => onDeletee(task.id)} /></h3>
            <p>{task.day}</p>
        </div>
    )
}