import React from 'react';
import './App.css';
import { useState, useEffect } from "react"
import Header from "./components/Header"
import MyTasks from './components/Tasks';
import AddTask from './components/AddTask';


function App() {
  const [showAddTask, setshowAddTask] = useState(false)
  const [tasks, setTask] = useState([])

  useEffect(() => {
    const getTasks = async () => {
      const tasksFromServer = await fetchTasks()
      setTask(tasksFromServer)
    }

    getTasks()

  }, [])
  // fetch tasks
  const fetchTasks = async () => {
    const res = await fetch("http://localhost:5000/tasks")

    const data = await res.json()

    return data
  }

  const fetchTask = async (id) => {
    const res = await fetch(`http://localhost:5000/tasks/${id}`)

    const data = await res.json()

    return data
  }

  const onDelete = async (id) => {
    await fetch(`http://localhost:5000/tasks/${id}`, {method:"DELETE"})
    setTask(tasks.filter((task) => task.id !== id))
  }

  const onAdd = async(task) => {
    const res = await fetch(`http://localhost:5000/tasks`, {method:"POST", headers:{
      'Content-type': "application/json",
    }, body:JSON.stringify(task)})

    const data = await res.json()

    setTask([...tasks, data])
    //const id = Math.floor(Math.random() * 10000) + 1

    //console.log(id)
  }

  const ToggleReminder = async (id) => {
    const taskToToggle = await fetchTask(id)
    const updTask = { ...taskToToggle, reminder: !taskToToggle.reminder }

    const res = await fetch(`http://localhost:5000/tasks/${id}`, {
      method: 'PUT',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify(updTask),
    })

    const data = await res.json()

    setTask(
      tasks.map((task) =>
        task.id === id ? { ...task, reminder: data.reminder } : task
      )
    )
  }
  return(
    <div className='container'>
      <Header clicked={() => {setshowAddTask(!showAddTask)}} txt={showAddTask}/>
      {showAddTask && <AddTask onAdd={onAdd} /> }
      
      {tasks.length >  0?<MyTasks tasks={tasks} key={tasks.id} onDelete={onDelete} onToggle={ToggleReminder}/>:"No tasks to show"}
    </div>
  )
}

export default App


